import React from 'react';
import { createPortal } from 'react-dom';

import './LanguagePicker.scss';

const Language = props => {

    if(props.mobileNav) {
        return createPortal(
            <div className="language-picker__container mobile-nav">
                <h5>
                    <span 
                        className={
                            `language-picker__portugues ${props.language === 'pt' ? 'active' : ''}`
                        }
                        onClick={props.changeLanguage}
                    >
                        Português
                    </span> 
                    <span> | </span>
                    <span 
                        className={
                            `language-picker__english ${props.language === 'en' ? 'active' : ''}`
                        }
                        onClick={props.changeLanguage}
                    >
                        English
                    </span> 
                </h5>
            </div>, 
            document.getElementById('language-picker')
        );
    } else {
        return (
            <div className="language-picker__container">
                <h5>
                    <span 
                        className={
                            `language-picker__portugues ${props.language === 'pt' ? 'active' : ''}`
                        }
                        onClick={props.changeLanguage}
                    >
                        Português
                    </span> 
                    <span> | </span>
                    <span 
                        className={
                            `language-picker__english ${props.language === 'en' ? 'active' : ''}`
                        }
                        onClick={props.changeLanguage}
                    >
                        English
                    </span> 
                </h5>
            </div>
        );
    }
    
};

export default Language;