import React, { useContext, useState } from 'react';

import LanguageContext from  '../../config/context/languageContext';
import Project from './Project/Project';
import ProjectList from './ProjectList/ProjectList';

import projects from '../../assets/data/projects';

import './Projects.scss';

const Projects = props => {
    const language = useContext(LanguageContext);
    const [loadedProject,  setLoadedProject] = useState(0); 

    const content = {
        pt: {
            title: 'Projetos',
            other: 'Outros Projetos'
        },
        en: {
            title: 'Projects',
            other: 'Other Projects'
        }
    };

    const loadProjectHandler = event => {
        const projectId = +event.target.closest('.list-item__container').id;

        setLoadedProject(projectId);
    }

    return (
        <div className="my-works__container">
            <h1>{content[language].title}</h1>
            <Project
                language={language}
                project={projects[loadedProject]}
            />
            <br/>
            <h2>{content[language].other}</h2>
            <ProjectList
                language={language}
                projects={projects}
                loadProject={loadProjectHandler}
                isMobile={props.isMobile}
            />
        </div>
    )
};

export default Projects;