import React, { useState, useEffect } from 'react';

import ProjectListItem from './ProjectListItem/ProjectListItem';

import './ProjectList.scss';

const ProjectList = props => {
    const projects = props.projects
    const [ page, setPage ] = useState(1);
    const [listProjects, setListProjects] = useState(projects.slice(0, 4));


    useEffect(() => {
        let updatedProjects;
        if(page === 1) {
            updatedProjects = projects.slice(0, 4);
            setListProjects(updatedProjects);
        } else if(page === 2) {
            updatedProjects = projects.slice(4, 8);
            setListProjects(updatedProjects);
        } else if(page === 3) {
            updatedProjects = projects.slice(8, 12);
            setListProjects(updatedProjects);
        }
        
    }, [page, projects]);

    const nextPageHandler = () => {
        let totalPages = Math.ceil(projects.length/4);
        let updatedPage = page + 1;

        document.querySelector('.arrow-left').classList.remove('fade');

        if(updatedPage === totalPages ) {
            document.querySelector('.arrow-right').classList.add('fade');
        }

        document.querySelectorAll('.list-item__container').forEach(item => {
            item.classList.add('fade');
        });

        setTimeout(() => {
            setPage(updatedPage);

            document.querySelectorAll('.list-item__container').forEach(item => {
                item.classList.remove('fade');
            });
        }, 800);        
    }

    const previousPageHandler = () => {
        let updatedPage = page - 1;

        document.querySelector('.arrow-right').classList.remove('fade');

        if(updatedPage === 1) {
            document.querySelector('.arrow-left').classList.add('fade');
        }

        document.querySelectorAll('.list-item__container').forEach(item => {
            item.classList.add('fade');
        });        

        setTimeout(() => {
            setPage(updatedPage);

            document.querySelectorAll('.list-item__container').forEach(item => {
                item.classList.remove('fade');
            });
        }, 800);              
    }

    const language = props.language;
    
    return (
        <div className="project-list__container">
             <div 
                className="arrow-left fade"
                onClick={previousPageHandler}
            >
            </div>
            <div 
            className="project-list__items">
                { props.isMobile && 
                        projects.map((project, index) => {
                            return (
                                <ProjectListItem
                                    loadProject={props.loadProject}
                                    title={project[language].title}
                                    key={index}
                                    imgUrl={project.imgUrl}
                                    id={index}
                                />
                            );
                        })
                } 
                { !props.isMobile &&
                    listProjects.map((project, index) => {
                        return (
                            <ProjectListItem
                                loadProject={props.loadProject}
                                title={project[language].title}
                                key={index}
                                imgUrl={project.imgUrl}
                                id={index}
                            />
                        );
                    })
                }                
            </div>
            <div 
                className="arrow-right"
                onClick={nextPageHandler}
            >
            </div>
        </div>
    )
};

export default ProjectList;