import React from 'react';

import './Techs.scss';

const Techs = props => {
    const techIcons = {
        html: 'fab fa-html5',
        css: 'fab fa-css3-alt',
        javascript: 'fab fa-js',
        node: 'fab fa-node',
        react: 'fab fa-react',
        sass: 'fab fa-sass'
    }
    return (
        <div className="techs__container">
            <h3>{props.language === 'pt' ? 'Tecnologias Utilizadas' : 'Technologies Used' }</h3>
            <div className="techs__icons">
                {props.techs.map(tech => {
                    return (
                        <i 
                            className={techIcons[tech]}
                            key={techIcons[tech]}
                        >
                        </i>
                    );
                })}
            </div>
        </div>
    )
}

export default Techs;