import React, { useState, useEffect } from 'react';
import { HashRouter as Router, Switch, Route, Redirect } from 'react-router-dom';

import LanguageContext from  './config/context/languageContext';

import MainNav from './Components/MainNav/MainNav';
import HamburgerButton from './Components/HamburgerButton/HamburgerButton';
import MobileNav from './Components/MobileNav/MobileNav';
import Overlay from './Components/Overlay/Overlay';
import LanguagePicker from './Components/LanguagePicker/LanguagePicker';

import Home from './Pages/Home/Home';
import About from './Pages/About/About';
import Projects from './Pages/Projects/Projects';
import Skills from './Pages/Skills/Skills';
import Contact from './Pages/Contact/Contact';
import Blog from './Pages/Blog/Blog';

import './App.scss';

function App() {
  const [language, setLanguage] = useState('pt');
  const [mobileNavIsOpen, setMobileNavIsOpen] = useState(false);

  useEffect(() => {
    const query = window.location.toString().split('?')[1];

    if(query) {
      const lang = query.split('=')[1];
      setLanguage(lang);
    }
  }, []);

  const [isMobile, setIsMobile] = useState(() => {
    if (window.innerWidth > 600) {
      return false;
    } else {
      return true;
    }
  });

  const changeLanguageHandler = event => {
    event.target.classList.contains('language-picker__portugues') ? setLanguage('pt') : setLanguage('en');
  }

  window.addEventListener("resize", () => {
    if (window.innerWidth > 600) {
      setIsMobile(false);
    } else {
      setIsMobile(true);
    }
  })

  const mobileNavStatusHandler = () => {
    const mobileNav = document.querySelector('.mobile-nav__container');
    const hamburgerMenu = document.querySelector('.hamburger-menu__container');
    const overlay = document.querySelector('.overlay');


    if(mobileNav) {
      mobileNav.classList.add('leave');
      overlay.classList.add('leave');
      hamburgerMenu.classList.remove('open');

      setTimeout(() => {
        setMobileNavIsOpen(false);
      }, 500);
      return;
    }
    hamburgerMenu.classList.add('open');
    setMobileNavIsOpen(true);
  }
  
  return (
    <div className="app__container">
      <Router>
          <LanguageContext.Provider value={language}>
            <MainNav 
              className="app__container-nav"
              changeLanguage={changeLanguageHandler}
            />
            < LanguagePicker 
              mobileNav={true}
              language={language}
              changeLanguage={changeLanguageHandler}
            />
            <HamburgerButton 
              mobileNavStatusHandler={mobileNavStatusHandler}
            />
            {mobileNavIsOpen && 
              < React.Fragment>
                < Overlay onClick={mobileNavStatusHandler}/>
                <MobileNav
                  mobileNavStatusHandler={mobileNavStatusHandler}
                />
              </React.Fragment>
            }
            <main>
              <div className="app__container-main">
                <Switch>
                  <Route exact path="/" >
                    < Home/>
                  </Route>
                  <Route exact path="/about" >
                    <About/>
                  </Route>
                  <Route exact path="/my-work" >
                    <Projects isMobile={isMobile}/>
                  </Route>
                  <Route exact path="/my-skills" >
                    <Skills isMobile={isMobile}/>
                  </Route>
                  <Route exact path="/blog" >
                    <Blog language={language}/>
                  </Route>
                  <Route exact path="/contact" >
                    <Contact language={language}/>
                  </Route>
                  < Redirect to="/" />
                </Switch>
              </div>
            </main>
          </LanguageContext.Provider>
        </Router>
      </div>
  );
}

export default App;
