import React from 'react';

import './Mockup.scss';

const Mockup = props => {
    const language = props.language;

    const content = {
        pt: {
            github: 'Código no Github',
            livePreview: 'Ver Site'
        },
        en: {
            github: 'See Code on Github',
            livePreview: 'Live Preview'
        }
    }

    return (
        <div className="project__mockup-container">
            <div className="project__mockup">
                <a href={props.livePreview} target="_blank" rel="noopener noreferrer">
                    <img src={props.mockupPicture} alt="Website mockup" className="project__mockup-image"></img>
                </a>
            </div>
            <div className="project__mockup-links">
                <a href={props.livePreview} target="_blank" rel="noopener noreferrer">
                    <i className="fas fa-globe"></i> {content[language].livePreview}
                </a>
                <a href={props.github} target="_blank" rel="noopener noreferrer">
                    <i className="fab fa-github"></i>  {content[language].github}
                </a>
            </div>
        </div>
    )
}

export default Mockup;