import React from 'react';

import Mockup from './Mockup/Mockup';
import Description from './Description/Description';

import './Project.scss';

const Project = props => {
    const language = props.language;

    return (
        <div className="project__container">
            < Mockup 
                mockupPicture={props.project.imgUrl}
                github={props.project.githubLink}
                livePreview={props.project.livePreview}
                language={language}
            />
            < Description 
                title={props.project[language].title}
                description={props.project[language].description}
                techs={props.project.techs}
                language={language}
            />
        </div>
    )
};

export default Project;